import React, {Component} from 'react';
import { getSummary } from '../../redux/personal.redux';
import { Icon,Modal } from 'antd-mobile';
import MySongList from '../../components/my-song-list/my-song-list';
import { Link } from 'react-router-dom';
import './me.less'
import { connect } from 'react-redux';
const prompt = Modal.prompt;
@connect(
    state=>state.personal,
    { getSummary }
)
class Me extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userId:""
        };
        this.newSongList = this.newSongList.bind(this)
    }
    componentDidMount(){
        this.setState({
            userId:sessionStorage.getItem('userId')
        },()=>{
            this.props.getSummary(this.state.userId)
        })

    }
    newSongList(){
        prompt(
            '', '', [
                { text: '' },
                { text: '', onPress: value => console.log(`:${value}`) },
            ]
        )}

    render() {
        return (
            <div id="me">
                {
                    this.props.summary?
                        <div className="me-wrapper">
                            <div className="personal">
                                <div className="left">
                                    <div className="avatar">
                                        <img src={this.props.summary.avatar} alt=""/>
                                    </div>
                                    <div className="nick-name">{this.props.summary.nickName}</div>
                                </div>
                                <div className="right">
                                    <Icon type="right"></Icon>
                                </div>
                            </div>
                            <Link to={`/collectsongs`} className="personal-item">
                                <div className="title"></div>
                                <div className="right">
                                    <Icon type="right"></Icon>
                                </div>
                            </Link>
                            <Link to={`/collectsonglist/${this.state.userId}`} className="personal-item">
                                <div className="title"></div>
                                <div className="right">
                                    <Icon type="right"></Icon>
                                </div>
                            </Link>
                            <div className="white-space"></div>
                            <div className="my-song-list">
                                <div className="top">
                                    <div className="left"></div>
                                    <div className="right" onClick={this.newSongList}>
                                        <span></span>
                                        <span>+</span>
                                    </div>
                                </div>
                                {
                                    this.props.summary.mySongList.length === 0?
                                        <div className="no-list">~</div>
                                        :
                                        <MySongList data={this.props.summary.mySongList}/>
                                }

                            </div>
                        </div>
                        :
                        ""
                }

            </div>
        )
    }
}
export default Me


